
import {defineComponent} from "vue"
import {dot} from "@/types/types"
export default defineComponent({
    data(){
        return {
            dots: [
                {
                    id:1,
                    active: true,
                },
                {
                    id:2,
                    active:false,
                },
                {
                    id:3,
                    active:false,
                },
                {
                    id:4,
                    active:false,
                },
                {
                    id:5,
                    active:false,
                },
                {
                    id:6,
                    active:false,
                },
            ] as dot[]
        }
    },
    methods: {
        dotActive(index:number):void{
            this.dots.forEach((el:dot):void => {
                el.active = false
            })
            this.dots[index].active = true
        }
    }
})
